<script lang="ts">
  import { onMount, onDestroy } from 'svelte'
  import { spectrumData } from '$lib/stores/websocket/hackrf'
  import type { SpectrumData } from '$lib/services/websocket/types'
  
  export let width = 800
  export let height = 400
  export let minDb = -120
  export let maxDb = -20
  export let colormap: 'viridis' | 'plasma' | 'inferno' | 'turbo' = 'turbo'
  export let scrollSpeed = 2
  
  let canvas: HTMLCanvasElement
  let ctx: CanvasRenderingContext2D | null
  let offscreenCanvas: HTMLCanvasElement
  let offscreenCtx: CanvasRenderingContext2D | null
  
  // Color maps for different visualizations
  const colormaps = {
    viridis: generateViridisColormap(),
    plasma: generatePlasmaColormap(),
    inferno: generateInfernoColormap(),
    turbo: generateTurboColormap()
  }
  
  onMount(() => {
    ctx = canvas.getContext('2d', { alpha: false })
    if (!ctx) return
    
    // Set canvas size with device pixel ratio
    const dpr = window.devicePixelRatio || 1
    canvas.width = width * dpr
    canvas.height = height * dpr
    canvas.style.width = `${width}px`
    canvas.style.height = `${height}px`
    ctx.scale(dpr, dpr)
    
    // Create offscreen canvas for waterfall data
    offscreenCanvas = document.createElement('canvas')
    offscreenCanvas.width = width
    offscreenCanvas.height = height
    offscreenCtx = offscreenCanvas.getContext('2d', { alpha: false })
    
    // Initialize with black
    if (offscreenCtx) {
      offscreenCtx.fillStyle = '#000000'
      offscreenCtx.fillRect(0, 0, width, height)
    }
  })
  
  onDestroy(() => {
    // Cleanup
  })
  
  // React to spectrum data updates
  $: if ($spectrumData && ctx && offscreenCtx) {
    updateWaterfall($spectrumData)
  }
  
  function updateWaterfall(data: SpectrumData) {
    if (!ctx || !offscreenCtx || !data.magnitude || data.magnitude.length === 0) return
    
    // Shift existing waterfall data down
    const imageData = offscreenCtx.getImageData(0, 0, width, height - scrollSpeed)
    offscreenCtx.putImageData(imageData, 0, scrollSpeed)
    
    // Draw new spectrum line at top
    const magnitudes = data.magnitude
    const binWidth = width / magnitudes.length
    const colorMap = colormaps[colormap]
    
    for (let i = 0; i < magnitudes.length; i++) {
      const magnitude = magnitudes[i]
      const normalized = (magnitude - minDb) / (maxDb - minDb)
      const colorIndex = Math.floor(Math.max(0, Math.min(255, normalized * 255)))
      const color = colorMap[colorIndex]
      
      offscreenCtx.fillStyle = color
      offscreenCtx.fillRect(i * binWidth, 0, Math.ceil(binWidth), scrollSpeed)
    }
    
    // Copy to main canvas
    ctx.drawImage(offscreenCanvas, 0, 0)
    
    // Draw frequency scale overlay
    drawFrequencyScale(data)
  }
  
  function drawFrequencyScale(data: SpectrumData) {
    if (!ctx) return
    
    const startFreq = data.centerFrequency - data.sampleRate / 2
    const endFreq = data.centerFrequency + data.sampleRate / 2
    const freqRange = endFreq - startFreq
    
    // Semi-transparent background for labels
    ctx.fillStyle = 'rgba(0, 0, 0, 0.7)'
    ctx.fillRect(0, height - 20, width, 20)
    
    ctx.fillStyle = '#ffffff'
    ctx.font = '10px monospace'
    ctx.textAlign = 'center'
    
    // Draw frequency labels
    const numLabels = 5
    for (let i = 0; i <= numLabels; i++) {
      const x = (width / numLabels) * i
      const freq = startFreq + (freqRange / numLabels) * i
      const label = formatFrequency(freq)
      
      ctx.fillText(label, x, height - 5)
    }
    
    // Draw time scale on the side
    drawTimeScale()
  }
  
  function drawTimeScale() {
    if (!ctx) return
    
    ctx.fillStyle = 'rgba(0, 0, 0, 0.7)'
    ctx.fillRect(width - 60, 0, 60, height)
    
    ctx.fillStyle = '#ffffff'
    ctx.font = '10px monospace'
    ctx.textAlign = 'right'
    
    const timeSteps = 5
    for (let i = 0; i <= timeSteps; i++) {
      const y = (height / timeSteps) * i
      const seconds = (y / scrollSpeed) * 0.1 // Approximate time
      ctx.fillText(`${seconds.toFixed(1)}s`, width - 5, y + 10)
    }
  }
  
  function formatFrequency(hz: number): string {
    if (hz >= 1e9) {
      return `${(hz / 1e9).toFixed(2)}G`
    } else if (hz >= 1e6) {
      return `${(hz / 1e6).toFixed(2)}M`
    }
    return `${(hz / 1e3).toFixed(0)}k`
  }
  
  // Color map generation functions
  function generateTurboColormap(): string[] {
    const map: string[] = []
    for (let i = 0; i < 256; i++) {
      const t = i / 255
      const r = Math.floor(255 * (t < 0.5 ? 4 * t : 1))
      const g = Math.floor(255 * Math.sin(Math.PI * t))
      const b = Math.floor(255 * (t > 0.5 ? 4 * (1 - t) : 1))
      map.push(`rgb(${r}, ${g}, ${b})`)
    }
    return map
  }
  
  function generateViridisColormap(): string[] {
    const map: string[] = []
    for (let i = 0; i < 256; i++) {
      const t = i / 255
      const r = Math.floor(255 * (0.267 + 0.004 * t + 0.329 * t * t))
      const g = Math.floor(255 * (0.000 + 0.985 * t - 0.158 * t * t))
      const b = Math.floor(255 * (0.330 + 0.458 * t - 0.788 * t * t))
      map.push(`rgb(${r}, ${g}, ${b})`)
    }
    return map
  }
  
  function generatePlasmaColormap(): string[] {
    const map: string[] = []
    for (let i = 0; i < 256; i++) {
      const t = i / 255
      const r = Math.floor(255 * (0.050 + 2.329 * t - 2.614 * t * t + 0.734 * t * t * t))
      const g = Math.floor(255 * (0.020 + 0.129 * t + 0.863 * t * t - 0.331 * t * t * t))
      const b = Math.floor(255 * (0.530 + 0.855 * t - 1.285 * t * t + 0.421 * t * t * t))
      map.push(`rgb(${r}, ${g}, ${b})`)
    }
    return map
  }
  
  function generateInfernoColormap(): string[] {
    const map: string[] = []
    for (let i = 0; i < 256; i++) {
      const t = i / 255
      const r = Math.floor(255 * (t < 0.5 ? 4 * t : 1))
      const g = Math.floor(255 * (t < 0.5 ? 0 : 2 * (t - 0.5)))
      const b = Math.floor(255 * (1 - t))
      map.push(`rgb(${r}, ${g}, ${b})`)
    }
    return map
  }
</script>

<div class="waterfall-display">
  <canvas
    bind:this={canvas}
    style="width: {width}px; height: {height}px;"
  />
  
  <div class="controls">
    <label>
      Color Map:
      <select bind:value={colormap}>
        <option value="turbo">Turbo</option>
        <option value="viridis">Viridis</option>
        <option value="plasma">Plasma</option>
        <option value="inferno">Inferno</option>
      </select>
    </label>
    
    <label>
      Speed:
      <input
        type="range"
        bind:value={scrollSpeed}
        min="1"
        max="5"
        step="1"
      />
    </label>
  </div>
</div>

<style>
  .waterfall-display {
    position: relative;
    background: #000000;
    border: 1px solid #333;
    border-radius: 4px;
    overflow: hidden;
  }
  
  canvas {
    display: block;
  }
  
  .controls {
    position: absolute;
    top: 10px;
    right: 10px;
    display: flex;
    gap: 10px;
    padding: 5px 10px;
    background: rgba(0, 0, 0, 0.8);
    border-radius: 4px;
    font-size: 12px;
  }
  
  label {
    display: flex;
    align-items: center;
    gap: 5px;
    color: #ffffff;
  }
  
  select, input[type="range"] {
    background: #1a1a1a;
    border: 1px solid #444;
    color: #ffffff;
    padding: 2px 5px;
    border-radius: 3px;
  }
  
  input[type="range"] {
    width: 80px;
  }
</style>