<script lang="ts">
  import { onMount, onDestroy } from 'svelte'
  import { spectrumData } from '$lib/stores/websocket/hackrf'
  import type { SpectrumData } from '$lib/services/websocket/types'
  
  interface DetectedSignal {
    id: string
    frequency: number
    strength: number
    bandwidth: number
    firstSeen: Date
    lastSeen: Date
    detectionCount: number
    active: boolean
  }
  
  export let threshold = -70 // dB threshold for detection
  export let minBandwidth = 10e3 // Minimum bandwidth in Hz
  export let maxSignals = 50 // Maximum number of signals to track
  
  let detectedSignals: Map<string, DetectedSignal> = new Map()
  let signalsList: DetectedSignal[] = []
  let sortBy: 'frequency' | 'strength' | 'bandwidth' | 'lastSeen' = 'strength'
  let sortDescending = true
  let autoScroll = true
  let selectedSignal: DetectedSignal | null = null
  
  // Signal detection parameters
  const SIGNAL_TIMEOUT = 5000 // Mark signal as inactive after 5 seconds
  const CLEANUP_INTERVAL = 10000 // Clean up old signals every 10 seconds
  
  let cleanupTimer: number
  
  onMount(() => {
    // Start cleanup timer
    cleanupTimer = setInterval(cleanupSignals, CLEANUP_INTERVAL)
  })
  
  onDestroy(() => {
    if (cleanupTimer) {
      clearInterval(cleanupTimer)
    }
  })
  
  // Process spectrum data for signal detection
  $: if ($spectrumData) {
    detectSignals($spectrumData)
  }
  
  // Sort signals when sort criteria changes
  $: signalsList = Array.from(detectedSignals.values()).sort((a, b) => {
    let comparison = 0
    
    switch (sortBy) {
      case 'frequency':
        comparison = a.frequency - b.frequency
        break
      case 'strength':
        comparison = a.strength - b.strength
        break
      case 'bandwidth':
        comparison = a.bandwidth - b.bandwidth
        break
      case 'lastSeen':
        comparison = a.lastSeen.getTime() - b.lastSeen.getTime()
        break
    }
    
    return sortDescending ? -comparison : comparison
  })
  
  function detectSignals(data: SpectrumData) {
    if (!data.magnitude || data.magnitude.length === 0) return
    
    const magnitudes = data.magnitude
    const binWidth = data.sampleRate / magnitudes.length
    const startFreq = data.centerFrequency - data.sampleRate / 2
    
    // Find signals above threshold
    let inSignal = false
    let signalStart = 0
    let signalPeak = -Infinity
    let signalSum = 0
    let signalBins = 0
    
    for (let i = 0; i < magnitudes.length; i++) {
      const magnitude = magnitudes[i]
      
      if (magnitude >= threshold && !inSignal) {
        // Start of signal
        inSignal = true
        signalStart = i
        signalPeak = magnitude
        signalSum = magnitude
        signalBins = 1
      } else if (magnitude >= threshold && inSignal) {
        // Continue signal
        signalPeak = Math.max(signalPeak, magnitude)
        signalSum += magnitude
        signalBins++
      } else if (magnitude < threshold && inSignal) {
        // End of signal
        inSignal = false
        
        const bandwidth = signalBins * binWidth
        if (bandwidth >= minBandwidth) {
          const centerBin = signalStart + Math.floor(signalBins / 2)
          const frequency = startFreq + centerBin * binWidth
          const avgStrength = signalSum / signalBins
          
          updateSignal(frequency, signalPeak, bandwidth)
        }
      }
    }
    
    // Handle signal that extends to the end
    if (inSignal) {
      const bandwidth = signalBins * binWidth
      if (bandwidth >= minBandwidth) {
        const centerBin = signalStart + Math.floor(signalBins / 2)
        const frequency = startFreq + centerBin * binWidth
        const avgStrength = signalSum / signalBins
        
        updateSignal(frequency, signalPeak, bandwidth)
      }
    }
    
    // Mark inactive signals
    const now = Date.now()
    detectedSignals.forEach((signal) => {
      if (now - signal.lastSeen.getTime() > SIGNAL_TIMEOUT) {
        signal.active = false
      }
    })
  }
  
  function updateSignal(frequency: number, strength: number, bandwidth: number) {
    const id = `${Math.round(frequency / 1000)}` // Round to nearest kHz for ID
    const now = new Date()
    
    if (detectedSignals.has(id)) {
      // Update existing signal
      const signal = detectedSignals.get(id)!
      signal.frequency = frequency
      signal.strength = strength
      signal.bandwidth = bandwidth
      signal.lastSeen = now
      signal.detectionCount++
      signal.active = true
    } else {
      // Add new signal
      if (detectedSignals.size < maxSignals) {
        detectedSignals.set(id, {
          id,
          frequency,
          strength,
          bandwidth,
          firstSeen: now,
          lastSeen: now,
          detectionCount: 1,
          active: true
        })
      }
    }
    
    // Trigger reactivity
    detectedSignals = detectedSignals
  }
  
  function cleanupSignals() {
    const now = Date.now()
    const timeout = 60000 // Remove signals not seen for 1 minute
    
    detectedSignals.forEach((signal, id) => {
      if (now - signal.lastSeen.getTime() > timeout) {
        detectedSignals.delete(id)
      }
    })
    
    // Trigger reactivity
    detectedSignals = detectedSignals
  }
  
  function clearSignals() {
    detectedSignals.clear()
    detectedSignals = detectedSignals
    selectedSignal = null
  }
  
  function selectSignal(signal: DetectedSignal) {
    selectedSignal = signal
    
    // TODO: Emit event to tune to this frequency
    // updateHackRFConfig({ frequency: signal.frequency })
  }
  
  function formatFrequency(hz: number): string {
    if (hz >= 1e9) {
      return `${(hz / 1e9).toFixed(6)} GHz`
    } else if (hz >= 1e6) {
      return `${(hz / 1e6).toFixed(6)} MHz`
    } else if (hz >= 1e3) {
      return `${(hz / 1e3).toFixed(3)} kHz`
    }
    return `${hz.toFixed(0)} Hz`
  }
  
  function formatBandwidth(hz: number): string {
    if (hz >= 1e6) {
      return `${(hz / 1e6).toFixed(2)} MHz`
    } else if (hz >= 1e3) {
      return `${(hz / 1e3).toFixed(1)} kHz`
    }
    return `${hz.toFixed(0)} Hz`
  }
  
  function formatDuration(ms: number): string {
    if (ms < 1000) {
      return `${ms}ms`
    } else if (ms < 60000) {
      return `${(ms / 1000).toFixed(1)}s`
    } else {
      return `${Math.floor(ms / 60000)}m ${Math.floor((ms % 60000) / 1000)}s`
    }
  }
</script>

<div class="signal-detection-list">
  <div class="header">
    <h3>Detected Signals ({signalsList.length})</h3>
    <div class="controls">
      <label>
        Threshold:
        <input
          type="number"
          bind:value={threshold}
          min="-120"
          max="-20"
          step="5"
        /> dB
      </label>
      
      <label>
        <input
          type="checkbox"
          bind:checked={autoScroll}
        />
        Auto-scroll
      </label>
      
      <button on:click={clearSignals}>Clear All</button>
    </div>
  </div>
  
  <div class="sort-controls">
    <button
      class="sort-button"
      class:active={sortBy === 'frequency'}
      on:click={() => { sortBy = 'frequency'; sortDescending = !sortDescending }}
    >
      Frequency {#if sortBy === 'frequency'}{sortDescending ? 'â–¼' : 'â–²'}{/if}
    </button>
    
    <button
      class="sort-button"
      class:active={sortBy === 'strength'}
      on:click={() => { sortBy = 'strength'; sortDescending = !sortDescending }}
    >
      Strength {#if sortBy === 'strength'}{sortDescending ? 'â–¼' : 'â–²'}{/if}
    </button>
    
    <button
      class="sort-button"
      class:active={sortBy === 'bandwidth'}
      on:click={() => { sortBy = 'bandwidth'; sortDescending = !sortDescending }}
    >
      Bandwidth {#if sortBy === 'bandwidth'}{sortDescending ? 'â–¼' : 'â–²'}{/if}
    </button>
    
    <button
      class="sort-button"
      class:active={sortBy === 'lastSeen'}
      on:click={() => { sortBy = 'lastSeen'; sortDescending = !sortDescending }}
    >
      Last Seen {#if sortBy === 'lastSeen'}{sortDescending ? 'â–¼' : 'â–²'}{/if}
    </button>
  </div>
  
  <div class="signal-list" class:auto-scroll={autoScroll}>
    {#each signalsList as signal (signal.id)}
      <div
        class="signal-item"
        class:active={signal.active}
        class:selected={selectedSignal?.id === signal.id}
        on:click={() => selectSignal(signal)}
      >
        <div class="signal-status" class:active={signal.active} />
        
        <div class="signal-info">
          <div class="frequency">{formatFrequency(signal.frequency)}</div>
          <div class="details">
            <span class="strength">{signal.strength.toFixed(1)} dB</span>
            <span class="bandwidth">{formatBandwidth(signal.bandwidth)}</span>
            <span class="count">Ã—{signal.detectionCount}</span>
          </div>
        </div>
        
        <div class="signal-time">
          <div class="duration">
            {formatDuration(signal.lastSeen.getTime() - signal.firstSeen.getTime())}
          </div>
          <div class="last-seen">
            {signal.active ? 'Active' : formatDuration(Date.now() - signal.lastSeen.getTime()) + ' ago'}
          </div>
        </div>
      </div>
    {/each}
    
    {#if signalsList.length === 0}
      <div class="empty-state">
        No signals detected above {threshold} dB
      </div>
    {/if}
  </div>
</div>

