<script lang="ts">
  import type { HTMLAttributes } from 'svelte/elements';
  
  interface CardProps extends HTMLAttributes<HTMLDivElement> {
    variant?: 'default' | 'elevated' | 'bordered';
    padding?: 'none' | 'sm' | 'md' | 'lg';
    class?: string;
  }
  
  let {
    variant = 'default',
    padding = 'md',
    class: className = '',
    children,
    ...restProps
  }: CardProps = $props();
  
  const variantClasses = {
    default: 'bg-surface-100 dark:bg-surface-800',
    elevated: 'bg-surface-100 dark:bg-surface-800 shadow-lg',
    bordered: 'bg-surface-50 dark:bg-surface-900 border border-surface-300 dark:border-surface-700'
  };
  
  const paddingClasses = {
    none: '',
    sm: 'p-3',
    md: 'p-6',
    lg: 'p-8'
  };
  
  const baseClasses = 'rounded-lg transition-colors duration-200';
  
  const cardClasses = $derived(`${baseClasses} ${variantClasses[variant]} ${paddingClasses[padding]} ${className}`);
</script>

<div
  class={cardClasses}
  {...restProps}
>
  {@render children?.()}
</div>