<script lang="ts">
  import { hackrfStatus, updateHackRFConfig, isConnected } from '$lib/stores/websocket/hackrf'
  import type { HackRFStatus } from '$lib/services/websocket/types'
  
  // Frequency presets
  const frequencyPresets = [
    { name: 'FM Radio', freq: 100e6, sampleRate: 2.4e6 },
    { name: '2m Amateur', freq: 145e6, sampleRate: 2.4e6 },
    { name: '70cm Amateur', freq: 435e6, sampleRate: 8e6 },
    { name: 'ADS-B', freq: 1090e6, sampleRate: 2e6 },
    { name: 'GPS L1', freq: 1575.42e6, sampleRate: 2.048e6 },
    { name: 'WiFi 2.4GHz', freq: 2437e6, sampleRate: 20e6 },
    { name: 'WiFi 5GHz', freq: 5180e6, sampleRate: 20e6 }
  ]
  
  let frequencyInput = ''
  let frequencyUnit = 'MHz'
  let sampleRateInput = ''
  let sampleRateUnit = 'MHz'
  let gain = 20
  let amplifierEnabled = false
  
  // Update inputs when status changes
  $: if ($hackrfStatus) {
    updateInputsFromStatus($hackrfStatus)
  }
  
  function updateInputsFromStatus(status: HackRFStatus) {
    // Convert frequency to appropriate unit
    if (status.frequency >= 1e9) {
      frequencyInput = (status.frequency / 1e9).toFixed(3)
      frequencyUnit = 'GHz'
    } else if (status.frequency >= 1e6) {
      frequencyInput = (status.frequency / 1e6).toFixed(3)
      frequencyUnit = 'MHz'
    } else {
      frequencyInput = (status.frequency / 1e3).toFixed(3)
      frequencyUnit = 'kHz'
    }
    
    // Convert sample rate to appropriate unit
    if (status.sampleRate >= 1e6) {
      sampleRateInput = (status.sampleRate / 1e6).toFixed(3)
      sampleRateUnit = 'MHz'
    } else {
      sampleRateInput = (status.sampleRate / 1e3).toFixed(3)
      sampleRateUnit = 'kHz'
    }
    
    gain = status.gain
    amplifierEnabled = status.amplifierEnabled
  }
  
  function parseFrequency(value: string, unit: string): number {
    const num = parseFloat(value)
    if (isNaN(num)) return 0
    
    switch (unit) {
      case 'GHz': return num * 1e9
      case 'MHz': return num * 1e6
      case 'kHz': return num * 1e3
      default: return num
    }
  }
  
  function applySettings() {
    if (!$isConnected) return
    
    const frequency = parseFrequency(frequencyInput, frequencyUnit)
    const sampleRate = parseFrequency(sampleRateInput, sampleRateUnit)
    
    if (frequency > 0 && sampleRate > 0) {
      updateHackRFConfig({
        frequency,
        sampleRate,
        gain,
        amplifierEnabled
      })
    }
  }
  
  function applyPreset(preset: typeof frequencyPresets[0]) {
    if (!$isConnected) return
    
    updateHackRFConfig({
      frequency: preset.freq,
      sampleRate: preset.sampleRate,
      gain,
      amplifierEnabled
    })
  }
  
  function handleKeyPress(event: KeyboardEvent) {
    if (event.key === 'Enter') {
      applySettings()
    }
  }
  
  function stepFrequency(direction: number) {
    const currentFreq = parseFrequency(frequencyInput, frequencyUnit)
    const step = parseFrequency('1', frequencyUnit) * 0.1 // 100 kHz steps
    const newFreq = currentFreq + (direction * step)
    
    updateHackRFConfig({
      frequency: newFreq,
      sampleRate: parseFrequency(sampleRateInput, sampleRateUnit),
      gain,
      amplifierEnabled
    })
  }
</script>

<div class="frequency-controls">
  <div class="control-section">
    <h3>Frequency Control</h3>
    
    <div class="frequency-input-group">
      <button 
        class="step-button" 
        on:click={() => stepFrequency(-1)}
        disabled={!$isConnected}
      >
        â—€
      </button>
      
      <input
        type="number"
        bind:value={frequencyInput}
        on:keypress={handleKeyPress}
        placeholder="Frequency"
        disabled={!$isConnected}
        step="0.001"
      />
      
      <select bind:value={frequencyUnit}>
        <option value="kHz">kHz</option>
        <option value="MHz">MHz</option>
        <option value="GHz">GHz</option>
      </select>
      
      <button 
        class="step-button" 
        on:click={() => stepFrequency(1)}
        disabled={!$isConnected}
      >
        â–¶
      </button>
    </div>
    
    <div class="sample-rate-group">
      <label>
        Sample Rate:
        <input
          type="number"
          bind:value={sampleRateInput}
          on:keypress={handleKeyPress}
          disabled={!$isConnected}
          step="0.001"
        />
        <select bind:value={sampleRateUnit}>
          <option value="kHz">kHz</option>
          <option value="MHz">MHz</option>
        </select>
      </label>
    </div>
  </div>
  
  <div class="control-section">
    <h3>Gain Control</h3>
    
    <div class="gain-controls">
      <label class="gain-slider">
        <span>VGA Gain: {gain} dB</span>
        <input
          type="range"
          bind:value={gain}
          on:change={applySettings}
          min="0"
          max="62"
          step="2"
          disabled={!$isConnected}
        />
      </label>
      
      <label class="amplifier-toggle">
        <input
          type="checkbox"
          bind:checked={amplifierEnabled}
          on:change={applySettings}
          disabled={!$isConnected}
        />
        <span>14dB Amplifier</span>
      </label>
    </div>
  </div>
  
  <div class="control-section">
    <h3>Frequency Presets</h3>
    
    <div class="presets">
      {#each frequencyPresets as preset}
        <button
          class="preset-button"
          on:click={() => applyPreset(preset)}
          disabled={!$isConnected}
        >
          {preset.name}
        </button>
      {/each}
    </div>
  </div>
  
  <button 
    class="apply-button"
    on:click={applySettings}
    disabled={!$isConnected}
  >
    Apply Settings
  </button>
</div>

