<script lang="ts">
  import { onMount } from 'svelte';
  import { Button, Card, Panel, StatusIndicator, GridLayout, ThemeToggle, ResizablePanel } from '$lib/components/ui';
  import { 
    SpectrumDisplay, 
    WaterfallDisplay, 
    FrequencyControls, 
    SignalDetectionList 
  } from './components';
  import { 
    connectHackRF, 
    disconnectHackRF, 
    isConnected, 
    hackrfStatus,
    connectionState
  } from '$lib/stores/websocket/hackrf';
  
  let displayMode: 'spectrum' | 'waterfall' | 'both' = 'both';
  let spectrumContainer: HTMLDivElement;
  let waterfallContainer: HTMLDivElement;
  let spectrumWidth = 800;
  let spectrumHeight = 300;
  let waterfallWidth = 800;
  let waterfallHeight = 300;
  
  onMount(() => {
    // Auto-connect on mount
    connectHackRF();
    
    // Handle window resize
    const handleResize = () => {
      if (spectrumContainer) {
        spectrumWidth = spectrumContainer.clientWidth - 20;
        spectrumHeight = displayMode === 'both' ? 300 : 500;
      }
      if (waterfallContainer) {
        waterfallWidth = waterfallContainer.clientWidth - 20;
        waterfallHeight = displayMode === 'both' ? 300 : 500;
      }
    };
    
    handleResize();
    window.addEventListener('resize', handleResize);
    
    return () => {
      // Cleanup on unmount
      disconnectHackRF();
      window.removeEventListener('resize', handleResize);
    };
  });
  
  function toggleConnection() {
    if ($isConnected) {
      disconnectHackRF();
    } else {
      connectHackRF();
    }
  }
</script>

<div class="min-h-screen bg-surface-50 dark:bg-surface-900">
  <!-- Header -->
  <header class="bg-surface-100 dark:bg-surface-800 border-b border-surface-200 dark:border-surface-700">
    <div class="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <div class="flex justify-between items-center h-16">
        <h1 class="text-xl font-semibold text-surface-900 dark:text-surface-100">
          HackRF SDR Dashboard
        </h1>
        <div class="flex items-center gap-4">
          <StatusIndicator 
            status={$isConnected ? 'online' : $connectionState.connecting ? 'warning' : 'offline'} 
            showLabel={true}
            label={$isConnected ? 'Connected' : $connectionState.connecting ? 'Connecting...' : 'Disconnected'}
          />
          <ThemeToggle variant="dropdown" />
        </div>
      </div>
    </div>
  </header>

  <!-- Main Content -->
  <main class="flex flex-col h-[calc(100vh-4rem)]">
    <div class="flex-1 flex gap-4 p-4 overflow-hidden">
      <!-- Left Panel: Controls and Signal Detection -->
      <div class="w-96 flex flex-col gap-4">
        <!-- Connection Control -->
        <Card variant="elevated" padding="sm">
          <Button 
            variant={$isConnected ? 'danger' : 'primary'}
            onclick={toggleConnection}
            class="w-full"
            disabled={$connectionState.connecting}
          >
            {$isConnected ? 'Disconnect' : $connectionState.connecting ? 'Connecting...' : 'Connect to HackRF'}
          </Button>
        </Card>
        
        <!-- Frequency Controls -->
        <FrequencyControls />
        
        <!-- Signal Detection List -->
        <div class="flex-1 min-h-0">
          <SignalDetectionList />
        </div>
      </div>
      
      <!-- Right Panel: Spectrum Visualization -->
      <div class="flex-1 flex flex-col gap-4">
        <!-- Display Mode Selector -->
        <Card variant="bordered" padding="sm">
          <div class="flex items-center justify-between">
            <h2 class="text-lg font-semibold">Spectrum Display</h2>
            <div class="flex gap-2">
              <Button
                variant={displayMode === 'spectrum' ? 'primary' : 'ghost'}
                size="sm"
                onclick={() => displayMode = 'spectrum'}
              >
                Spectrum
              </Button>
              <Button
                variant={displayMode === 'waterfall' ? 'primary' : 'ghost'}
                size="sm"
                onclick={() => displayMode = 'waterfall'}
              >
                Waterfall
              </Button>
              <Button
                variant={displayMode === 'both' ? 'primary' : 'ghost'}
                size="sm"
                onclick={() => displayMode = 'both'}
              >
                Both
              </Button>
            </div>
          </div>
        </Card>
        
        <!-- Spectrum Displays -->
        {#if displayMode === 'spectrum' || displayMode === 'both'}
          <div class="flex-1 min-h-0" bind:this={spectrumContainer}>
            <SpectrumDisplay width={spectrumWidth} height={spectrumHeight} />
          </div>
        {/if}
        
        {#if displayMode === 'waterfall' || displayMode === 'both'}
          <div class="flex-1 min-h-0" bind:this={waterfallContainer}>
            <WaterfallDisplay width={waterfallWidth} height={waterfallHeight} />
          </div>
        {/if}
        
        <!-- Status Bar -->
        {#if $hackrfStatus}
          <Card variant="bordered" padding="sm">
            <div class="flex items-center justify-between text-sm">
              <div class="flex gap-6">
                <span>Frequency: <strong>{($hackrfStatus.frequency / 1e6).toFixed(3)} MHz</strong></span>
                <span>Sample Rate: <strong>{($hackrfStatus.sampleRate / 1e6).toFixed(3)} MS/s</strong></span>
                <span>Gain: <strong>{$hackrfStatus.gain} dB</strong></span>
                <span>Amplifier: <strong>{$hackrfStatus.amplifierEnabled ? 'ON' : 'OFF'}</strong></span>
              </div>
              {#if $connectionState.error}
                <span class="text-danger-600 dark:text-danger-400">
                  Error: {$connectionState.error.message}
                </span>
              {/if}
            </div>
          </Card>
        {/if}
      </div>
    </div>
  </main>
</div>

<style>
  :global(html, body) {
    height: 100%;
    overflow: hidden;
  }
  
  :global(#app) {
    height: 100%;
  }
</style>

