<script lang="ts">
  import { onMount, onDestroy } from 'svelte'
  import { spectrumData } from '$lib/stores/websocket/hackrf'
  import type { SpectrumData } from '$lib/services/websocket/types'
  
  export let width = 800
  export let height = 300
  export let minDb = -120
  export let maxDb = -20
  export let gridLines = true
  export let smoothing = 0.7
  
  let canvas: HTMLCanvasElement
  let ctx: CanvasRenderingContext2D | null
  let animationFrameId: number
  let previousMagnitudes: number[] = []
  
  // Color scheme
  const backgroundColor = '#0f0f0f'
  const gridColor = '#333333'
  const spectrumColor = '#00ff00'
  const peakHoldColor = '#ff0000'
  const textColor = '#888888'
  
  // Peak hold
  let peakHold: number[] = []
  let peakHoldDecay = 0.95
  
  onMount(() => {
    ctx = canvas.getContext('2d')
    if (!ctx) return
    
    // Set canvas size with device pixel ratio for sharp rendering
    const dpr = window.devicePixelRatio || 1
    canvas.width = width * dpr
    canvas.height = height * dpr
    canvas.style.width = `${width}px`
    canvas.style.height = `${height}px`
    ctx.scale(dpr, dpr)
    
    // Start render loop
    render()
  })
  
  onDestroy(() => {
    if (animationFrameId) {
      cancelAnimationFrame(animationFrameId)
    }
  })
  
  function render() {
    if (!ctx) return
    
    // Clear canvas
    ctx.fillStyle = backgroundColor
    ctx.fillRect(0, 0, width, height)
    
    // Draw grid
    if (gridLines) {
      drawGrid()
    }
    
    // Draw spectrum
    const data = $spectrumData
    if (data) {
      drawSpectrum(data)
      drawFrequencyLabels(data)
    }
    
    animationFrameId = requestAnimationFrame(render)
  }
  
  function drawGrid() {
    if (!ctx) return
    
    ctx.strokeStyle = gridColor
    ctx.lineWidth = 0.5
    
    // Horizontal lines (dB levels)
    const dbStep = 20
    for (let db = minDb; db <= maxDb; db += dbStep) {
      const y = dbToY(db)
      ctx.beginPath()
      ctx.moveTo(0, y)
      ctx.lineTo(width, y)
      ctx.stroke()
      
      // Label
      ctx.fillStyle = textColor
      ctx.font = '10px monospace'
      ctx.fillText(`${db} dB`, 5, y - 2)
    }
    
    // Vertical lines (frequency divisions)
    const freqDivisions = 10
    for (let i = 0; i <= freqDivisions; i++) {
      const x = (width / freqDivisions) * i
      ctx.beginPath()
      ctx.moveTo(x, 0)
      ctx.lineTo(x, height)
      ctx.stroke()
    }
  }
  
  function drawSpectrum(data: SpectrumData) {
    if (!ctx || !data.magnitude || data.magnitude.length === 0) return
    
    const magnitudes = data.magnitude
    const binWidth = width / magnitudes.length
    
    // Smooth the spectrum data
    if (previousMagnitudes.length === magnitudes.length) {
      for (let i = 0; i < magnitudes.length; i++) {
        magnitudes[i] = magnitudes[i] * (1 - smoothing) + previousMagnitudes[i] * smoothing
      }
    }
    previousMagnitudes = [...magnitudes]
    
    // Update peak hold
    if (peakHold.length !== magnitudes.length) {
      peakHold = [...magnitudes]
    } else {
      for (let i = 0; i < magnitudes.length; i++) {
        if (magnitudes[i] > peakHold[i]) {
          peakHold[i] = magnitudes[i]
        } else {
          peakHold[i] *= peakHoldDecay
        }
      }
    }
    
    // Draw peak hold line
    ctx.strokeStyle = peakHoldColor
    ctx.lineWidth = 1
    ctx.globalAlpha = 0.5
    ctx.beginPath()
    for (let i = 0; i < peakHold.length; i++) {
      const x = i * binWidth + binWidth / 2
      const y = dbToY(peakHold[i])
      if (i === 0) {
        ctx.moveTo(x, y)
      } else {
        ctx.lineTo(x, y)
      }
    }
    ctx.stroke()
    ctx.globalAlpha = 1
    
    // Draw spectrum line
    ctx.strokeStyle = spectrumColor
    ctx.lineWidth = 2
    ctx.shadowBlur = 10
    ctx.shadowColor = spectrumColor
    
    ctx.beginPath()
    for (let i = 0; i < magnitudes.length; i++) {
      const x = i * binWidth + binWidth / 2
      const y = dbToY(magnitudes[i])
      if (i === 0) {
        ctx.moveTo(x, y)
      } else {
        ctx.lineTo(x, y)
      }
    }
    ctx.stroke()
    
    // Fill area under spectrum
    ctx.lineTo(width, height)
    ctx.lineTo(0, height)
    ctx.closePath()
    ctx.fillStyle = spectrumColor
    ctx.globalAlpha = 0.1
    ctx.fill()
    ctx.globalAlpha = 1
    ctx.shadowBlur = 0
  }
  
  function drawFrequencyLabels(data: SpectrumData) {
    if (!ctx) return
    
    const startFreq = data.centerFrequency - data.sampleRate / 2
    const endFreq = data.centerFrequency + data.sampleRate / 2
    const freqRange = endFreq - startFreq
    
    ctx.fillStyle = textColor
    ctx.font = '10px monospace'
    
    // Draw frequency labels
    const numLabels = 5
    for (let i = 0; i <= numLabels; i++) {
      const x = (width / numLabels) * i
      const freq = startFreq + (freqRange / numLabels) * i
      const label = formatFrequency(freq)
      
      const textWidth = ctx.measureText(label).width
      ctx.fillText(label, x - textWidth / 2, height - 5)
    }
  }
  
  function dbToY(db: number): number {
    const normalized = (db - minDb) / (maxDb - minDb)
    return height - (normalized * height)
  }
  
  function formatFrequency(hz: number): string {
    if (hz >= 1e9) {
      return `${(hz / 1e9).toFixed(3)} GHz`
    } else if (hz >= 1e6) {
      return `${(hz / 1e6).toFixed(3)} MHz`
    } else if (hz >= 1e3) {
      return `${(hz / 1e3).toFixed(3)} kHz`
    }
    return `${hz.toFixed(0)} Hz`
  }
</script>

<div class="spectrum-display">
  <canvas
    bind:this={canvas}
    style="width: {width}px; height: {height}px;"
  />
</div>

<style>
  .spectrum-display {
    position: relative;
    background: #0f0f0f;
    border: 1px solid #333;
    border-radius: 4px;
    overflow: hidden;
  }
  
  canvas {
    display: block;
  }
</style>